@import url("https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap");

.body {
  /* background-color: rgb(0, 0, 0); */
  background-image: url("https://media3.giphy.com/media/TZf4ZyXb0lXXi/200w.webp?cid=ecf05e47ci8hbeu0bl5jn3zx5j0ku1y0rv88wxv2wlbizcyb&rid=200w.webp&ct=g");
  background-size:cover;
  border: 0;
  color: white;
  font-family: nunito;
  font-weight: bolder;
  height: 90vh;
  margin: 0;
  /* padding: 60px; */
  text-align: center;
  width: 100%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.dash {
  background-color: #0b151d;
  border-radius: 180px 180px 10px 10px;
  display: inline-block;
  height: 300px;
  padding: 10px 70px;
  width: 90%;
  border-top: 1px solid white;
  border-right: 1px solid white;
  border-left: 1px solid white;
}

.alarmNaveFantasma {
  width: 100%;
  height: 20%;
}

.last-minute-data {
  float: left;
  border: 1px solid white;
  border-radius: 5px;
  direction: rtl;
  font-size: 0.7rem;
  height: 80%;
  min-width: 100px;
  overflow-y: scroll;
  padding: 5px;
  width: 150px;
}

.alerts {
  top: 10px;
  width: 300px;
  display: inline-block;
  float: right;
  font-size: 1rem;
}

.alertOk {
  margin-top: 10px;
  background-color: rgb(21, 66, 0);
  display: inline-block;
  padding: 2px;
  border-radius: 50px;
  width: 100%;
  color: rgb(255, 255, 255);
  float: right;
}

.alertDanger {
  margin-top: 10px;
  background-color: rgb(255, 0, 0);
  display: inline-block;
  padding: 10px;
  border-radius: 50px;
  width: 100%;
  color: rgb(255, 255, 255);
  float: right;
}

.btnLaunchRay{
  padding: 0px 2px;
  margin-top: 10px;
  font-size: 40px;
  color: black;
  background-color: rgb(255, 0, 0);
  color: rgb(255, 255, 255);
  display: inline-block;
  border-radius: 10px;
}


.danger {
  color: rgb(236, 0, 0);
}